.aboutMe {
    background-color: #000;
    display: flex;
    flex-direction: row;
    height: 100vh;
    justify-content: start;
}

.aboutMe_container {
    display: flex;
    width: 365px;
    height: 255px;
    margin-left: 20rem;
    margin-top: 13rem;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.socialMedia {
    display: flex;
    justify-content: space-between;
    width: 90%;
    height: fit-content;
    align-items: start;
}

.socialMedia img {
    width: 60px;
    height: 60px;
    transition: all 0.5s;
}

.socialMedia :hover {
    scale: 1.1;
}

.aboutMe_list {
    display: flex;
    width: fit-content;
    height: fit-content;
}